import { Box, Button, Flex, IconButton, Image, Menu, MenuButton, MenuItem, MenuList } from '@chakra-ui/react';
import React from 'react';
import img from "../../src/Images/img.png";
import styles from "../Styles/Navbar.module.css";
import { DownloadIcon, HamburgerIcon } from '@chakra-ui/icons';

const Navbar = () => {
  return (
    <Flex className={styles.box_shadow} justifyContent="space-around"  >
        <Box>
            <Image src={img} w="40%"/>
        </Box>
        <Flex className={styles.scroll_content} w="60%" fontSize="20px" fontWeight="bold" display={{base:"none", sm:"none", md:"none", lg:"flex"}}>
            <Flex>About Me</Flex>
            <Flex>Skills</Flex>
            <Flex>Projects</Flex>
            <Flex>GitHub Stats</Flex>
            <Flex>Contact Me</Flex>
            <a target="_blank" rel="noreferrer" href="https://drive.google.com/file/d/15o6A0qrMaHGVjzG8veE7v3y1DiYtCIvr/view?usp=share_link"><Button colorScheme='orange'>Resume<DownloadIcon/></Button></a>
        </Flex>
            <Menu>
              <MenuButton
                  display={{base:"block", sm:"block", md:"block", lg:"none"}}
                  as={IconButton}
                  aria-label='Options'
                  icon={<HamburgerIcon />}
                  variant='outline'
              />
              <MenuList>
                  <MenuItem >
                  About Me
                  </MenuItem>
                  <MenuItem >
                  Skills
                  </MenuItem>
                  <MenuItem >
                  Projects
                  </MenuItem>
                  <MenuItem >
                  GitHub Stats
                  </MenuItem>
                  <MenuItem >
                  Contact Me
                  </MenuItem>
                  <MenuItem >
                  Download Resume <DownloadIcon/>
                  </MenuItem>
              </MenuList>
          </Menu>
    </Flex>
  )
}

export default Navbar